import React, { Component } from 'react';
import { Table, Button } from 'antd';
import { setTableAdditionalRegistration } from 'dump/data';

class TableAddRegisterNotSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M hc phn',
        dataIndex: 'maHocPhan',
        key: 'maHocPhan',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'S TC',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'Tn lp tn ch',
        dataIndex: 'tenLopTC',
        key: 'tenLopTC',
      },
      {
        title: 'Lch hc',
        dataIndex: 'lichHoc',
        key: 'lichHoc',
      },
      {
        title: 'Gio vin',
        dataIndex: 'giaoVien',
        key: 'giaoVien',
      },
      {
        title: 'Phng hc',
        dataIndex: 'phongHoc',
        key: 'phongHoc',
      },
      {
        title: 'Cn trng',
        dataIndex: 'conTrong',
        key: 'conTrong',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button className="btnSmallAddInTable" onClick={this.showModal}>
            Thm
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTableAdditionalRegistration}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableAddRegisterNotSelected;



