import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Button, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, setTimesAdditionalRegistration } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Hc k:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Nm hc:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="K ng k">
              {getFieldDecorator('setTimesAdditionalRegistration', {})(
                <Select>
                  {setTimesAdditionalRegistration.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="M SV">
              <Input placeholder="Nhp m SV" />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label=" ">
              <Button type="primary">Tm kim</Button>
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={6}>
            S TC ti thiu: <b>16</b>
          </Col>
          <Col span={6}>
            S TC ti a: <b>24</b>
          </Col>
          <Col span={6}>
            H tn: <b>Hong Th Nhung</b>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



