import React, { Component } from 'react';
import { Table, Button, Row, Col } from 'antd';
import { setTableAdditionalRegistration } from 'dump/data';
import { BtnSave, BtnAddNew } from 'components/common/button';

class TableAddRegisterSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M hc phn',
        dataIndex: 'maHocPhan',
        key: 'maHocPhan',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'S TC',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'Tn lp tn ch',
        dataIndex: 'tenLopTC',
        key: 'tenLopTC',
      },
      {
        title: 'Lch hc',
        dataIndex: 'lichHoc',
        key: 'lichHoc',
      },
      {
        title: 'Gio vin',
        dataIndex: 'giaoVien',
        key: 'giaoVien',
      },
      {
        title: 'Phng hc',
        dataIndex: 'phongHoc',
        key: 'phongHoc',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: () => (
          <Button className="btnSmallDeleteInTable" onClick={this.showModal}>
            Xa
          </Button>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Row gutter={24}>
          <Col span={8}>
            S hc phn: <b>3</b>
          </Col>
          <Col span={8}>
            S tn ch: <b>9</b>
          </Col>
          <Col span={8}>
            S tin phi np: <b>2000.000</b>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnAddNew title="ng k mn" />
              <BtnSave title="Xa mn" />
              <Button type="primary" className="btnEventOrtherPrimary">
                Lu
              </Button>
              <Button type="primary" className="btnEventOrtherPrimary">
                In phiu ng k
              </Button>
            </div>
          </Col>
        </Row>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTableAdditionalRegistration}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableAddRegisterSelected;



